body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  overflow: hidden;
}

h1, h2, h3, h4, h5 {
  margin: 0;
}

.board-wrapper {
  min-height: 50vmin;
  width: 80vmin;
  font-size: 2vmin;
  border-radius: 20px;
  border: 1px solid;
  margin-top: 20px;
  padding: 20px;
}

.term-wrapper {
  /* height: 15%; */
  width: 100%;
  padding: 10px 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 1px solid;
}

.definition-wrapper {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  /* height: 25%; */
  width: 100%;
  padding: 10px 0px;
  border-bottom: 1px solid;
}

#score-animation-wrapper {
  position: absolute;
  right: 5%;
  -webkit-transition: bottom 1s linear, opacity .35s linear;
  transition: bottom 1s linear, opacity .35s linear;
  color: lightgreen;
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
}

.slot-wrapper {
  width: calc(25% - 42px);
  /* height: 70%; */
  height: 60px;
  padding: 0px 10px;
  border: 1px dashed;
  border-radius: 10px;
  margin: 10px;
}

.deck-wrapper {
  /* height: 60%; */
  width: 100%;
  padding: 10px 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.card-wrapper {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: calc(25% - 42px);
  height: 60px;
  padding: 0px 10px;
  margin: 10px;
  border: 1px solid;
  border-radius: 10px;
  background-color: #282c34;
  -webkit-transition: none 0s ease 0s;
  transition: none 0s ease 0s;
  -webkit-transition: initial;
  transition: initial;
}

.card-wrapper p {
  pointer-events: none;
}

.card-wrapper:hover {
  cursor: -webkit-grab;
  cursor: grab;
}

.card-wrapper:active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.lives {
  max-width: 50px;
  height: auto;
  margin: 11px;
}

